@import url(https://fonts.googleapis.com/css2?family=Jura:wght@500&display=swap);
.NavBar{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}
.HiddenBar{
    display: -webkit-flex;
    display: flex;
    max-height: 0;
    overflow: hidden;
}
.NavBarElement {
    text-decoration: none;
    margin: 0 15px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.NavIcon {
    width: 100px;
    transition: 0.7s;
}
.NavIcon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.LogoImg {
    width: 150px;
}
.Categories {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.CategoriesTitle {
    font-size: 32px;
}
.Category {
    color: #aaa;
    text-decoration: none;
}
.Categories:hover {
    color: #fff;
}
.Calc {
    margin: auto;
    width: 300px;
}
label {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 20px 0;
}
.HomeInput {
    border-radius: 50px;
    height: 25px;
    border: none;
    width: 150px;
    padding: 0;
}
.submitButton {
    width: 150px;
    margin: 0;
    padding: 0;
    border-radius: 50px;
    height: 25px;
    border: none;
    font-family: 'Jura', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
}
body {
    background-image: url(/static/media/wallpaper.5f715d03.JPG);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    margin: 0;
    padding: 0;
    color: #fff;
    font-family: 'Jura', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
#root {
    width: 80%;
    margin: auto;
    background-color: rgba(44, 44, 44, 0.8);
    height: 100%;
}
